@import "./themes/theme.scss";

.App{
  margin: 0;
  padding: 0;
  width: 100vw;
  min-height: 100%;
}

.parallax {
  background-color: rgba(115, 100, 121, 0);
  height: 100%;
  background-attachment: sticky;
  background-position: center;
  background-size: cover
}

.section{
  min-height: 100vh;
}

.container-box{
  margin: 0vh 2vh 0vh 2vh;
}

.particles-box{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.description{
  font-weight: 100;
  padding: .5em;
}

h1 {
  padding-top: 0.25em;
  padding-bottom: 0.25em;
  text-align: center;
  font-weight: bolder;
  font-size: 3em;
}

$h3-shadow: #205f553a;
h3{
  color: $experience-title;
  text-shadow: none;
  font-size: 1.1em;
  font-weight: 300;
}

$h4-shadow: #2c2c2c00;
h4{
  color: $experience-subtitle;
  //text-shadow: 1px 1px .5px $h4-shadow, -1px 1px .5px $h4-shadow, 1px -1px .5px $h4-shadow, -1px -1px .5px $h4-shadow;;
  font-size: 1em;
  font-weight: 300;
}

.text-subtitle{
  font-weight: 400;
  color: $education-secundary;
  padding-left: 15px;
}

.text-description{
  font-weight: 300;
  color: $education-description;
  padding-left: 15px;
}

.nospace{
  margin: 0;
  padding: 0;
}

//buttons

.btn-outline-success{
  color: $success-botton;
  border-color: $success-botton;
}

h1{
  padding-top: 1.1em;
  padding-bottom: 0.7em;
  color: $titles;
  text-shadow: $shadow-white;
}

a {
  color: $purple;
  font-weight: bolder;
  font-weight: 500;
}

 a:hover{
  font-weight: 700;
  text-decoration: none;
  text-shadow: $links-color;
  color: white;
}
